.view-params {
    background-color: var(--white);
    position: absolute;
    width: 1000px;
    height: 32px;
    left: 35px;
    top: 65px;
    border-radius: 7px 7px 0 0;

    display: flex;
    flex-direction: row;
    align-content: center;
    flex-wrap: wrap;
    justify-content: space-around;
}

.autoscroll-box {
    display: flex;
    width: 220px;
    justify-content: space-between;
}

.font-control-box {
    display: flex;
    width: 170px;
    justify-content: space-between;
}

.scroll-regulator {
    align-self: center;
    width: 60px;
    height: 25px;

    box-sizing: border-box;
    border-radius: 8px;
    background-color: var(--wild-sand);

    display: flex;
    justify-content: space-evenly;
}

.scroll-regulator img {
    align-self: center;
    height: 25px;
    width: 25px;
    cursor: pointer;
}

.font-regulator {
    align-self: center;
    width: 60px;
    height: 25px;

    box-sizing: border-box;
    border-radius: 8px;
    background-color: var(--wild-sand);

    display: flex;
    justify-content: space-evenly;
}

.font-regulator img {
    align-self: center;
    height: 16px;
    width: 16px;
    cursor: pointer;
}

.rotated {
    transform: rotate(180deg);
}

.vline {
    left: 50%;
    right: 50%;
    top: 0;
    bottom: 100%;

    border: 1px solid var(--white);
}

.song-box {
    background-color: var(--white);

    position: absolute;
    left: 35px;
    top: 100px;
    bottom: 10px;

    overflow: hidden;
    overflow-y: scroll;

    width: 1000px;

    border-radius: 0 0 7px 7px;
}

.song-title-singer {
    position: relative;
    margin-left: 30px;
    margin-right: 5px;

    font-size: var(--font-size-xxxl);
    font-weight: 600;
    font-style: normal;
}

.song-header-box {
    position: relative;
    width: 100%;
    height: 40px;
    top: 10px;
    padding: 10px;

    font-size: var(--font-size-xxxl);
    font-weight: 600;
    font-style: normal;

    display: flex;
    flex-direction: row;
    align-content: center;
    align-items: baseline;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.song-chords {
    position: relative;
    margin: 20px 30px 30px 30px;

    font-size: var(--font-size-xl);
    font-weight: 600;
    font-style: normal;

    overflow: hidden;
    white-space: pre-line;
}

.chords-grid {
    width: 90%;
    margin-top: 20px;
    margin-left: 5%;
    margin-right: 5%;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.chord-pic {
    width: 70px;
    height: auto;
}

.singer-song-box {
    background-color: var(--white);
    position: absolute;
    width: 1000px;
    height: 45px;
    left: 35px;
    top: 65px;
    border-radius: 7px 7px 0 0;

    display: flex;
    flex-direction: row;
    align-content: center;
    flex-wrap: wrap;
    justify-content: space-around;
}

.input-song-box {
    background-color: var(--white);

    position: absolute;
    left: 35px;
    top: 115px;
    bottom: 80px;

    overflow: hidden;

    width: 1000px;

    border-radius: 0 0 7px 7px;
}

.singer-box {
    display: flex;
    width: 300px;
    justify-content: space-between;
    align-items: center;

    font-size: 15pt;
}

.singer-input {
    width: 210px;
    height: 25px;

    font-size: 13pt;
}

.song-name-box {
    display: flex;
    width: 380px;
    justify-content: space-between;
    align-items: center;

    font-size: 15pt;
}

.song-name-input {
    width: 260px;
    height: 25px;

    font-size: 13pt;
}

.lyrics-and-chords {
    position: relative;
    width: 904px;
    height: 10%;
    left: 50px;
    top: 13px;

    font-size: 17pt;
}

.song-lyrics {
    position: relative;
    margin: 0 5% 5% 5%;

    width: 90%;
    height: 85%;

    overflow: hidden;
    overflow-y: scroll;
    resize: none;

    font-family: inherit;
    font-size: inherit;
}

.create-song-button {
    position: absolute;
    bottom: 17px;
    left: 700px;

    align-items: center;
    background-color: var(--fern-green);
    border-radius: 12px;
    display: flex;
    justify-content: center;
    margin-top: 23px;
    height: 41px;
    min-width: 318px;
    text-align: center;

    outline: none;
    border: none;
    cursor: pointer;
}
